p{
    margin: 0;
}

.all-profile-container{
    height: 100%;
    width: 100%;
}



.profile-container{
    display:flex;
    position: relative;
    align-items:center;
    text-align: center;
    flex-direction: row;
    flex-wrap: wrap-reverse;
    justify-content:space-between;
    /* height: 10%; */
    min-height: -webkit-fit-content;
    min-height: -moz-fit-content;
    min-height: fit-content;
    background-color: var(--light-yellow);
    padding: 62px 10vw 64px 10vw;
    color: var(--light-purple);
    z-index: 0;

}


.colz{
    display: flex;
    position: relative;
    height: 32px;
}

.colz .colz-icon{
    display: inline;
    position: absolute;
    left: -8px;
}

.colz .colz-icon i{
    align-items: center;
    padding: 8px;
    color:  var(--light-purple);

}
.profile-details{

    height: 40vw;
    width: 36vw;

}

.profile-details-name{
    text-align: left;
    font-style: normal;
    font-weight:900;
    margin: 0;
}

.profile-details-role{
    text-align: left;
    font-style: normal;
    font-weight:900;
    margin: 0;
}

.primary-text{
    font-size: 32px;
    margin: 0;
}

.profile-short-introduction{
    font-size: 20px;
    font-weight: 300;
}

.profile-options{
    display: flex;
    position: relative;
    margin: 50px 0;
    height: 66px;
    
}
.profile-options-button{
    display: inline;
    position: absolute;
    font-size: 16px;
    left: 0;
}

.profile-options-button .highlighted-btn{
    font-weight: 900;
    padding: 16px 16px;
    color: rgb(255, 247, 240);
    border-radius: 52px;
    background-color:  var(--light-purple);
    border-color: transparent;
}

.profile-options-button .highlighted-btn:hover{
    background-color: rgb(255, 132, 0);
}



.profile-picture{
    display: flex;
    align-items: center;
    justify-content:center;
    height: 40vw;
    width: 36vw;
}

.profile-picture-background{
    margin-top: -60px;
    margin-right: 0px;
    height: 100%;
    width: 100%;
    background-image: url(/YiyangLi/static/media/Face.81b3493d.png);
    background-repeat: no-repeat;
    background-size: contain;
}
.profile-arrow{
    position: fixed;
    bottom: 10%;
    left: 0;
    right: 0;
    display: block;
    width: 60px;
    height: 50px;
    margin-left: auto;
    margin-right: auto;
    z-index: 0;

}

.profile-arrow-asset{
    width: 100%;
    height: 100%;
    display: block;
    
    background-image: url(/YiyangLi/static/media/Arrow.d869f3ac.svg);
    background-repeat: no-repeat;
    background-size: cover;
}


@media screen and (max-width:1110px){
    .profile-picture{
        /* height: 30vw;
        width: 30vw; */
        margin: 0 0 0 80px;
    }
    .primary-text{
        font-size: 20px;
    }
    .profile-short-introduction{
        font-size: 16px;
    }
    .profile-options-button{
        font-size: 16px;
    }
}

@media screen and (max-width:910px){
    .profile-picture{
        height: 30vw !important;
        width: 30vw !important;
        margin: 0 0 0 4px;
    }
}
@media screen and (max-width:810px){
    .profile-picture{
        width: 100vw !important;
        height: 100vw !important;
        margin: 0 !important;
    }
    .profile-details{
        width: 100vw;
        height: 100vw;
    }
    .profile-parent{
        flex-direction: column-reverse;
        margin: 40px 0 0 0;
    }
    .profile-options{
        display: flex;
        justify-content: space-between;
    }

}







.navbar{
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    height: 80px;
    width: 100%;
    overflow: hidden;
    display:block;
    justify-content: center;
    background-color: rgba(254, 246, 234, 0.5);
    -webkit-backdrop-filter: blur(18px);
            backdrop-filter: blur(18px);
    padding: 0 160px;
    z-index: 4;
}


.navbar-container{
    height: 100%;
    display: flex;
    align-items:center;
    justify-content:right;
    flex-direction: row-reverse;
    text-align: center;
}
.navbar-nav{
    display: block;
    text-align: center;
    
}

.nav-item{
    display: inline-block;
}

.nav-item .nav-link{
    font-size: 16px;
    font-weight: 900;
    color: var(--light-purple);
    padding: 0 0 0 32px;
}

.all-work-container{
    height: 100%;
    width: 100%;
    position: relative;
}

.work-container{
    width: 100%;
    display: flex;
    align-items:center;
    text-align: center;
    justify-content: left;
    flex-wrap: wrap;
    flex-direction: column;

    margin-top: 48px;
    margin-bottom: 60px;
    z-index: 2;
}



.all-work-container .title{
    position: absolute;
    top:0;
    display: block;
    width: 24vw;
    height: 20vw;
    z-index: 3;
    border: none;
}

.title-img{
    width: 100%;
    height: 100%;
    background-image: url(/YiyangLi/static/media/ExperienceDesign.2d03b865.png);
    background-repeat:no-repeat;
    background-size: contain;
    border: none;
    
}


.work-card{
    width: 100%;
    height: 45vh;
    min-height: 420px;
    display: flex;
    align-items:center;
    text-align: center;
    justify-content: left;
    flex-wrap: wrap;

}

.work-card-text{
    display: block;
    width: 50%;
    height: 100%;
    padding: 46px 64px 48px 64px;
    text-align: left;
    background-color: white;
    /* border-radius: 0 20px 20px 0 ; */
}

.work-card-text .work-time-stamp{
    font-style: normal;
    font-weight: 900;
    font-size: 14px;
    line-height: 23px;
    text-transform: uppercase;

    color: var(--light-gray);
    
}

.work-card-text .work-title{
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 46px;

    color: var(--light-purple);
    
}

.work-card-text .work-client{
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 23px;
    text-transform: uppercase;

    color: var(--light-gray);
    margin-bottom: 42px;
    
}

.work-card-text .work-explaination{
    width: 100%;
    font-weight: 300;
    font-size: 14px;
    line-height: 29px;
    margin-bottom: 64px;

    color: #5A5957;
}

.work-tags{
    display: flex;
    align-items:flex-start;
    text-align: center;
    justify-content: left;
    flex-wrap: wrap;
    flex-direction: row;

    bottom: 48px;
    margin-bottom: 0;
    padding: 0;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    
}

.work-tags li{
    display: block;
    list-style-type: none;
    
    padding: 10px 20px;
    margin-right: 8px;
    margin-bottom: 8px;
    background-color:var(--light-light-gray) ;
    border-radius: 20px;
    font-size: 14px;
    font-weight: 900;
    color: var(--light-gray);

}

.work-card .take-me-out{
    width: 50%;
    height: 100%;
    overflow: hidden;
    /* background-image: url('../../../assets/Home/TakeMeOut.png');
    background-repeat:no-repeat;
    background-size: cover; */
    /* border-radius: 20px 0 0 20px; */
}
.work-card .take-me-out img{
    width: 100%;
    height: 100%;
    background-image: url(/YiyangLi/static/media/TakeMeOut.231b0c19.png);
    background-repeat:no-repeat;
    background-size: cover; 

    /* border-radius: 20px 0 0 20px; */
}
.work-card img{
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: all 0.5s ease-in-out;

    /* border-radius: 20px 0 0 20px; */
}
.work-card:hover img{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    transition: all 0.5s ease-in-out;

    /* border-radius: 20px 0 0 20px; */
}

.work-card .cili{
    width: 50%;
    height:100%;
    overflow: hidden;
    /* background-image: url('../../../assets/Home/Cili.png');
    background-repeat:no-repeat;
    background-size: cover; */
    /* border-radius: 20px 0 0 20px; */
}

.work-card .cili img{
    width: 100%;
    height: 100%;
    background-image: url(/YiyangLi/static/media/Cili.b2df11cd.png);
    background-repeat:no-repeat;
    background-size: cover; 

    /* border-radius: 20px 0 0 20px; */
}

@media screen and (max-width:910px){
    .work-card-text{
        width: 100vw;
    }
    .take-me-out{
        width: 100vw !important;
    }
    .cili{
        width: 100vw !important;
    }
    .all-work-container .title{
        width: 80vw !important;
        height: 80vw !important;
    }
}
.home-container{
    display: flex;
    flex-direction: column;
    justify-content:flex-start;
    background-color: var(--light-yellow);
    height: 100%;
    min-height: 670px;
}
